import * as React from "react";
import { AppBar, Toolbar, IconButton } from "@material-ui/core";
import ShopIcon from '@material-ui/icons/ShoppingCart'
import NotifIcon from '@material-ui/icons/Notifications'
import SearchIcon from '@material-ui/icons/Search'
import MsgIcon from '@material-ui/icons/Message'
import UserIcon from '@material-ui/icons/AccountCircle'

// @ts-ignore
import logo from 'shared/images/Logo.png';
import { navigate } from "gatsby";

const Navbar = () => {
    const go = (url) => {
        return function () {
            navigate(url);
        }
    }
    return (
        <AppBar position="sticky">
            <Toolbar>
                <IconButton edge="start" onClick={go('/user/')} color="inherit" aria-label="menu">
                    <img src={logo} alt="" height="40px" />
                </IconButton>
                <div style={{ flexGrow: 1 }} />
                {process.env.GATSBY_APP_KIND === 'APP' && <>
                <IconButton edge="start" onClick={go('/user/search')} color="inherit" aria-label="menu">
                    <SearchIcon height="40px" />
                </IconButton>
                <IconButton edge="start" onClick={go('/user/cart')} color="inherit" aria-label="menu">
                    <ShopIcon height="40px" />
                </IconButton>
                </>}
                <IconButton edge="start" onClick={go('/user/mail')} color="inherit" aria-label="menu">
                    <MsgIcon height="40px" />
                </IconButton>
                <IconButton edge="start" onClick={go('/user/notif')} color="inherit" aria-label="menu">
                    <NotifIcon height="40px" />
                </IconButton>
                <IconButton edge="start" onClick={go('/user/profile')} color="inherit" aria-label="menu">
                    <UserIcon height="40px" />
                </IconButton>
            </Toolbar>
        </AppBar>
    );
}

export default Navbar;